import logo from './logo.svg';
import './App.css';
import { useState } from 'react'

function App() {
  const [text, setText] = useState('text');
  const [imgSrc, setImgSrc] = useState('noimg.png')
  return (
    <div className="App">
      <div className='ban-ao'>
        <div className='hinh'>
          <img alt='' src='tshirt.png' />
          <div className='hinh-container'>
            <div className='hinh-image'>
              <img alt='' src={imgSrc} className='hinh-img' />
              <input type='file' className='file-input' onChange={e => {
                const { files } = e.target;
                if (files.length > 0) {
                  const vUrl = URL.createObjectURL(files[0]);
                  setImgSrc(vUrl)
                }
              }} />
            </div>
          </div>
          <div className='hinh-text xl-font' readOnly >{text}</div>
        </div>
        <div className='noi-dung'>
          <div className='c-font sml-font'>Nike</div>
          <div className='c-font lg-font'>Air Max motion 2</div>
          <div className='customizable-text'>Customizable Text</div>
          <input className='custom-text' value={text} onChange={e => setText(e.target.value)} onFocus={(e) => e.target.select()} />
          <div className='customizable-text'>Upload Your Photo:</div>
          <div className='photo-upload'>Please click <label className='photo-text'>
            <input type='file' className='file-input' onChange={e => {
              const { files } = e.target;
              if (files.length > 0) {
                const vUrl = URL.createObjectURL(files[0]);
                setImgSrc(vUrl)
              }
            }} />here</label> to upload it now</div>

        </div>
      </div>
    </div>
  );
}

export default App;
